.hero {
  background: #ffb8ca;
  background-image: url('/images/ice-cream_320.png');

  background-size: auto 90%;
  background-repeat: no-repeat;
  background-position: bottom right -80px;
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url('/images/ice-cream_320@2x.png');
  }
  @media screen and (min-width: 768px) {
    background-image: none;
  }
  @media screen and (min-width: 1200px) {
  }
}
.hero__container {
  position: relative;
  padding-top: 194px;
  padding-bottom: 190px;
  background-repeat: no-repeat;
  @media screen and (min-width: 768px) {
    background-size: auto 90%;

    padding-top: 102px;

    padding-bottom: 190px;
    background-image: url('/images/ice-cream_768.png');
    background-position: bottom right 185px;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('/images/ice-cream_768@2x.png');
    }
  }
  @media screen and (min-width: 1200px) {
    background-image: url('/images/ice-cream_1200.png');

    background-position: bottom right 229px;
    padding-bottom: 84px;
    padding-top: 147px;
    padding-bottom: 316px;
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('/images/ice-cream_1200@2x.png');
    }
  }
}
.hero__title {
  position: relative;
  z-index: 2;
  margin-bottom: 20px;
  font-family: $titan-one;
  font-weight: 400;
  text-transform: uppercase;

  color: $main-bg-color;

  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  @media screen and (min-width: 768px) {
    font-size: 22px;
    line-height: 1.13;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 38px;
    line-height: 1.15;
  }
}
.hero-btn-list {
  display: flex;
  position: relative;
  z-index: 2;
  @media screen and (min-width: 768px) {
  }
  @media screen and (min-width: 1200px) {
  }
}
.hero-btn-list__item {
  margin-right: 8px;
}
.img-cafe-wrap {
  display: none;
  @media screen and (min-width: 768px) {
    display: block;
  }
}

.cafe-list {
  @media screen and (max-width: 767px) {
    display: none;
  }
}
.hero__text {
  font-family: $dm-sans;
  font-size: 8px;
  color: $main-bg-color;
  margin-bottom: 7px;
  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 140%;
    margin-bottom: 20px;
  }
}
.hero__wrap {
  display: none;
  position: absolute;

  left: 167px;
  bottom: 56px;
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: column;
    width: 115px;
  }
  @media screen and (min-width: 1200px) {
    left: 227px;
    bottom: 84px;
    width: 200px;
  }
}
.cafe-list {
  display: flex;
  flex-direction: column;
}
.cafe-list__item {
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.cafe-list__text {
  font-family: $titan-one;
  font-size: 28px;
  line-height: 1.14;

  text-align: right;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $main-accent-color;
  z-index: 5;
  @media screen and (min-width: 1200px) {
    font-size: 46px;
    line-height: 1.15;
  }
}
.cafe-list__description {
  color: $main-bg-color;
  font-family: 'DM Sans';
  font-size: 8px;
  line-height: 1.25;
  text-align: right;
  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 1.28;
  }
}
.hero__list-wrap {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.hero__img-wrap {
  display: none;
  position: absolute;

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    width: auto;
    width: 152px;
    left: 15px;
    bottom: 16px;
  }
  @media screen and (min-width: 1200px) {
    bottom: 16px;
    left: 15px;
    width: 213px;
    left: 15px;
    bottom: 60px;
  }
}
.hero__img-wrap-2 {
  display: none;

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    width: auto;
    width: 190px;
    height: auto;
    position: absolute;
    bottom: 0px;
    right: 15px;
    z-index: 2;
  }

  @media screen and (min-width: 1200px) {
    width: 304px;
  }
}
.hero__ice-wrap {
  position: absolute;
  left: 36px;
  bottom: 0px;
  z-index: 1;
  width: 346px;
  height: auto;
  @media screen and (min-width: 768px) {
    width: 335px;
    bottom: 0px;
    left: 248px;
  }
  @media screen and (min-width: 1200px) {
    width: 534px;

    bottom: 0px;
    left: 500px;
  }
}
.arrow-wrap {
  border-radius: 50%;
  width: 18px;
  height: 18px;
  background-color: #ffffff;
  fill: $main-accent-color;
  display: flex;
  align-items: center;
  justify-content: center;
  @media screen and (min-width: 1200px) {
    width: 26px;
    height: 26px;
  }

  &:hover,
  &:focus {
    background-color: #ecebeb;
  }
}

.arrow-btn {
  width: 8px;
  height: 8px;
}
.cafe-list__item::before {
  content: '';
  width: 16px;
  height: 16px;
  background-color: #ffffff;
  position: absolute;
  top: 0px;
  right: 0px;
  border-radius: 50%;
  z-index: 4;
}

@keyframes Appearance {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
.section__title--hero {
  font-family: $titan-one;
  font-weight: 400;

  font-size: 48px;
  line-height: 1.147;
  letter-spacing: 0.04em;

  text-align: center;
  text-transform: uppercase;

  color: $main-accent-color;

  @media screen and (min-width: $medium) {
    font-size: 34px;
    line-height: 1.145;
  }

  @media screen and (min-width: $large) {
    font-size: 58px;
    line-height: 1.138;
    letter-spacing: 0.06em;
  }
}
